<template>
  <div id="image_carousel">
    <h3>Image Carousel</h3>
    <div class="image_carousel_main">
      <transition-group
        tag="div"
        class="div-slider"
        :name="back ? 'slideback' : 'slide'"
      >
        <div
          v-for="index in [currentIndex]"
          :key="index"
          class="image_container"
        >
          <div class="card">
            <img :src="imageURL(index)" @click="selectImage(index)" />
          </div>
        </div>
      </transition-group>
    </div>
    <div class="buttons">
      <button @click="prev()">prev</button>
      <button @click="next()">next</button>
    </div>
    <div class="selected_items">
      <div v-for="item in selectedImages" :key="item">{{ item }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ImageCarousel",
  data: () => ({
    back: false,
    currentIndex: 0,
    local_data: [],
    selectedImages: [],
  }),
  computed: {
    currentImageURL: function () {
      return this.local_data[
        Math.abs(this.currentIndex) % this.local_data.length
      ]?.download_url;
    },
  },
  methods: {
    next: function () {
      this.back = false;
      this.currentIndex++;
    },
    prev: function () {
      this.back = true;
      this.currentIndex--;
    },
    imageURL: function (index) {
      return this.local_data[Math.abs(index) % this.local_data.length]
        ?.download_url;
    },
    selectImage: function (index) {
      const { download_url } =
        this.local_data[Math.abs(index) % this.local_data.length];

      if (download_url) {
        const index = this.selectedImages.findIndex(
          (item) => item === download_url
        );

        if (index === -1) {
          this.selectedImages.push(download_url);
        } else {
          this.selectedImages.splice(index, 1);
        }
      }
    },
    getPicts: function () {
      fetch("https://picsum.photos/v2/list", {
        method: "GET",
      })
        .then((response) => response.json())
        .then((data) => {
          this.local_data = data;
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  mounted() {
    this.getPicts();
  },
};
</script>

<style scoped>
#image_carousel {
  overflow: hidden;
  height: 100%;
  flex-flow: column;
  display: flex;
}

.slide-leave-active,
.slide-enter-active,
.slideback-leave-active,
.slideback-enter-active {
  transition: 1s;
}

.image_carousel_main {
  flex: 1 1 auto;
  display: flex;
}

.div-slider {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  height: 100%;
  width: 100%;
}

.div-slider .image_container {
  display: flex;
  height: 100%;
  width: 100%;
  position: absolute;
  justify-content: center;
}

.div-slider .card {
  display: flex;
  height: 100%;
}

.card img {
  height: auto;
  width: 100%;
  object-fit: cover;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: center;
}

.selected_items {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
}

.slide-enter {
  transform: translate(100%, 0);
}
.slide-leave-to {
  transform: translate(-100%, 0);
}

.slideback-enter {
  transform: translate(-100%, 0);
}
.slideback-leave-to {
  transform: translate(100%, 0);
}
</style>
